.selector{
    margin-top: 90px;
    background-image: url("../../../public/clinic/clinicaigeneratedpicture.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100vh;
    overflow-y: auto;
}
.searchBar {
    position: fixed;
    z-index: 1;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    border: black;
    border-radius: 12px;
    background-color: #FFFFFF;
    color: #2d3efa;
    font-size: 16px;
    font-weight: bold;
    padding: 12px 24px;
    cursor: text;
    transition: all 0.2s ease-in-out;
    width: 340px;
    box-shadow: 0 2px 4px rgba(1, 1, 1, 1);
}
